* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Open Sans', sans-serif;
  font-size: 20px;
}
div.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  margin: 0 auto;

  color: #222
}

header {
  flex-basis: 45vh;

}

main {
  display: flex;
  flex-grow: 1;
  padding: 30px 20px;
  max-width: 1600px;

  /* margin: 0 auto; */

}

aside {
  min-width: 240px;

}

.page {

  flex-grow: 1;
}

footer {
  background-color: black;
  color: #bbb;
  padding: 10px 20px;
  font-size: 16px;


}

article {
  margin-top: 30px
}

article h3 {
  margin-bottom: 5px;
  text-transform: uppercase;
}

article span {
  display: block;
  margin-bottom: 10px;
  font-size: 15px;
}

article p {
  font-size: 18px;
}
header {
    position: relative;
    overflow: hidden;
}

header img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;

}
nav.main {

    padding: 5px 30px 30px 5px;
}

.main ul {
    list-style: none;
}

nav.main a {
    display: block;
    text-decoration: none;
    color: black;
    text-transform: uppercase;
    font-weight: bold;
    padding: 10px;
    transition: 0.2s;
}

nav.main a:hover {
    transform: translateX(20px);
}

nav.main li:nth-child(1) a.active {
    border-left: 10px rgb(16, 54, 43) solid;
    transform: translateX(0);
}

nav.main li:nth-child(2) a.active {
    border-left: 10px rgb(32, 102, 49) solid;
    transform: translateX(0);
}

nav.main li:nth-child(3) a.active {
    border-left: 10px rgb(27, 126, 60) solid;
    transform: translateX(0);
}

nav.main li:nth-child(4) a.active {
    border-left: 10px rgb(11, 156, 11) solid;
    transform: translateX(0);
}
.advice h1 {
    margin-bottom: 10px;
}

.advice p {
    margin-bottom: 10px;
}
a {
    color: black;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}
.advices {
    display: block;
    margin-left: 20%;


}

.advices h2 {
    margin-bottom: 20px;
    text-transform: uppercase;
}

.advices ul {
    list-style: square;
    padding-left: 20px;

}

.advices li {
    margin-bottom: 20px;
}

.advices a {
    text-decoration: none;
    color: #333;
    transition: 0.2s;
}

.advices a:hover {
    text-decoration: underline;
    color: black;
    font-size: 21px;
}
.contact {
    max-width: 1600px;


}

.contact textarea {
    font-size: 16px;
    min-width: 400px;
    max-width: 600px;
    min-height: 200px;
    margin: 20px auto;
    display: block;
    line-height: 1.6;
    font-family: 'open sans';
    box-shadow: 0 0 1px 1px black;
    background-color: #eee;
    border: none;


}

.contact button {
    display: block;
    margin: 0 20% 0 auto;
    border: 2px solid black;
    background-color: #fff;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 20px;
    font-family: 'open sans';
    transition: 0.2s;
    text-transform: uppercase;
    letter-spacing: 2px;
    border-radius: 5%;
}

.contact h3 {
    text-align: center;
    text-shadow: gray 1px 1px;
}

.contact button:hover {
    background-color: black;
    color: white;
}
.login {

    display: flex;
    flex-direction: column;
    margin: 0 auto;
    text-align: center;
}

.login input {
    width: 250px;
    height: 30px;
    margin-bottom: 20px;
    font-family: 'open sans';
}

.login label {
    max-width: 1024px;
}

.login button {
    width: 150px;
    height: 40px;
    margin: 20px auto;
    text-transform: uppercase;
    border: 2px solid black;
    background-color: #fff;
    font-family: 'open sans';
    font-size: 20px;
    letter-spacing: 2px;
    cursor: pointer;

}
